.order-form label {
  display: block;
  margin-top: 20px;
}

.order-form input {
  width: 100%;
  border-radius: 10px;
  outline: none;
  border: 1px solid grey;
  padding: 5px 10px;
}

.order-form input:required::placeholder {
  font-size: 1.4rem;
  color: darksalmon;
}

.error {
  color: red;
}